<template>
  <h1>make it go blip blop</h1>
  <div class="sequencer">
    <div
      v-for="(row, rowIndex) in grid"
      :id="rowIndex"
      :key="rowIndex"
      class="row"
    >
      <!-- <div
        v-if="rowIndex === 0"
        class="stepper"
        :class="{ move: playing }"
        ref="stepper"
      ></div> -->
      <button
        v-for="(note, noteIndex) in row"
        :id="noteIndex"
        :key="noteIndex"
        class="note"
        :class="{ active: note.isActive, playing: note.isPlaying }"
        @click="noteClick(rowIndex, noteIndex)"
      ></button>
    </div>
  </div>
  <div class="control-buttons">
    <button class="control-button" @click="start">
      <img v-if="!playing" src="../assets/play-solid.svg" />
      <img v-if="playing" src="../assets/pause-solid.svg" />
    </button>
    <button class="control-button" @click="reset">
      <img src="../assets/eraser-solid.svg" />
    </button>
  </div>
</template>

<script>
import * as Tone from "tone";

let synths = undefined;

export default {
  name: "Sequencer",
  data() {
    return {
      started: false,
      playing: false,
      beat: 0,
      notes: ["C4", "D4", "E4", "F4", "G4", "A4"],
      grid: undefined,
    };
  },
  mounted() {
    synths = this.makeSynths(6);
    this.grid = this.makeGrid(this.notes);
  },

  methods: {
    async start() {
      if (!this.started) {
        await Tone.start();
        Tone.getDestination().volume.rampTo(-10, 0.001);
        this.configLoop();
        this.started = true;
      }

      if (this.playing) {
        this.pause();
      } else {
        Tone.Transport.start();
        this.playing = true;
      }
    },
    reset() {
      this.grid.forEach((row) => {
        row.forEach((note) => {
          note.isActive = false;
          note.isPlaying = false;
        });
      });
      this.stop();
    },
    stop() {
      this.pause();
      this.beat = 0;
    },
    pause() {
      Tone.Transport.stop();
      this.playing = false;
    },
    makeSynths(count) {
      const synths = [];

      for (let i = 0; i < count; i++) {
        const synth = new Tone.PolySynth().toDestination();
        synth.set({ detune: -1000 });
        synths.push(synth);
      }

      return synths;
    },
    makeGrid(notes) {
      const rows = [];

      for (const note of notes) {
        const row = [];

        for (let i = 0; i < 8; i++) {
          row.push({
            note: note,
            isActive: false,
            isPlaying: false,
          });
        }

        rows.push(row);
      }

      return rows;
    },
    configLoop() {
      const repeat = (time) => {
        this.grid.forEach((row, index) => {
          let synth = synths[index];
          let note = row[this.beat];

          if (note.isActive) {
            note.isPlaying = true;
            setTimeout(() => (note.isPlaying = false), 300);
            synth.triggerAttackRelease(note.note, "8n", time);
          }
        });

        this.beat = (this.beat + 1) % 8;
      };

      Tone.Transport.bpm.value = 120;
      Tone.Transport.scheduleRepeat(repeat, "8n");
    },
    noteClick(clickedRowIndex, clickedNoteIndex) {
      this.grid.forEach((row, rowIndex) => {
        row.forEach((note, noteIndex) => {
          if (clickedRowIndex === rowIndex && clickedNoteIndex === noteIndex) {
            note.isActive = !note.isActive;
          }
        });
      });
    },
  },
};
</script>

